.wrapper{
    background: hsla(0, 0%, 0%, 0.918);
    min-height: 100vh;
}

.wrapper-header{
    background: hsla(0, 0%, 0%, 0.767);
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
}

.wrapper-header h1{
    color:  rgba(0, 225, 255, 0.945);
}

.wrapper-header h3{
    color: #fff;
}

.left-items{
    display: flex;
    gap: 3px;
}

.wrapper-logo{
    width: 30px;
}

.react-icon{
    width: 30%;
}

.right-docker img{
    position: absolute;
    top: 15rem;
    right: 0;
    opacity: 0.09;
}


.content-text{
    margin: 8rem 30px;
    color: #fff;
}

.content-text h2{
    font-size: 50px;
}

.content-text p{
    font-size: 20px;
    padding: 20px 0;
    margin-left: 5px;
    position: relative;
}

.text{
    display: flex;
    align-items: center;
    margin-left: 2rem;
}

