*{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

.container{
  width: 88%;
  margin: 0 auto;
}

.dot{
  background: aqua;
  width: 8px;
  height: 8px;
  aspect-ratio: 1/1;
  border-radius: 100%;
  align-items: center;
}